html, body {
  position: absolute;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-accent-500);
  background: linear-gradient(45deg, var(--color-accent-500) 0%, var(--color-primary-300-4) 45%, var(--color-primary-400) 100%);
}
* {
  font-family: var(--primary-font-family);
  letter-spacing: var(--letter-spacing);
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
input {
  -webkit-user-select: text;
